#root {
  font-family: "Raleway", "Arial", sans-serif;
  font-size: 0.875rem;
  position: relative;
}
.side-navigation .side-nav-link {
  position: absolute;
  color: white;
  right: -3.125rem;
  transition: 0.5s;
  padding: 0.3125rem 0.3125rem 0.3125rem 0.625rem;
  margin: 0;
  width: 6.25rem;
  text-decoration: none;
  font-size: 1.625rem;
  border-radius: 0.3125rem 0 0 0.3125rem;
}

.side-navigation {
  position: fixed;
  right: 0;
  top: 0;
  height: calc(100vh + 0.9375rem);
  width: 6.25rem;
  z-index: 10;
}
.side-navigation .active {
  right: 0;
  color: #262626;
  font-weight: bold;
}
.side-navigation .side-nav-link:hover {
  right: 0;
}

.side-nav-link.active {
  background-color: #ffd480;
}
.side-nav-link:nth-child(1) {
  top: 0.9375rem;
}
.side-nav-link:nth-child(2) {
  top: 4.6875rem;
}
.side-nav-link:nth-child(3) {
  top: 8.4375rem;
}
.side-nav-link:nth-child(4) {
  top: 12.1875rem;
}
.side-nav-link:nth-child(5) {
  top: 15.9375rem;
}

.side-nav-link.login-button {
  top: 19.6875rem;
  border: none;
  color: #262626;
}

