#root {
  font-family: "Raleway", "Arial", sans-serif;
  font-size: 0.875rem;
  position: relative;
}
.basic-main-container {
  padding: 0 5vw;
}

.basic-main-row {
  min-height: 100vh;
  padding: 0 0 6.25rem 0;
}

.basic-row-height {
  height: calc((40rem - 6.25rem - 1.25rem)/ 3 - 1.25rem);
}

.basic-row-m-p {
  margin: 0 0 0.625rem 0;
  padding: 0.3125rem;
}

.basic-col-m-p {
  margin: 0.625rem;
  padding: 0.3125rem;
}

.basic-image-size {
  height: auto;
  max-width: 100%;
  max-height: calc(calc((40rem - 6.25rem - 1.25rem)/ 3 - 1.25rem) - 0.625rem);
}

.basic-dark-bg {
  background-color: #262626;
}

.basic-pink-bg {
  background-color: #fa9eb5;
}

.basic-mint-bg {
  background-color: #a8f0d8;
}

.basic-beige-bg {
  background-color: #fce6cf;
}

.basic-gold-bg {
  background-color: #ffd480;
}

.basic-show-all-button {
  background-color: #262626;
  color: white;
  padding: 0.625rem;
  margin: 1.25rem;
  border-radius: 0.3125rem;
}

.basic-link-hover-gray:hover {
  color: gray;
}

@media (max-width: 992px) {
  footer {
    height: 12.5rem;
  }

  .basic-main-row {
    padding: 0 0 12.5rem 0;
  }

  .basic-col-m-p {
    margin: 0.625rem 0;
    padding: 0.3125rem;
  }

  .basic-row-m-p {
    margin: 0.625rem;
    padding: 0.3125rem;
  }
  .basic-row-m-p.xs {
    margin: 0;
    padding: 0;
  }

  .basic-row-height.xs-h {
    height: 100%;
  }
}

